	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Another fine moment in systems administration","date":"15 Mar 2004","slug":"/2004-03-15-another-fine-moment-in-systems-administration/","tags":"Y2004"},"html":"<p>A month ago I replaced the system on a monitoring server, a low-powered PC that stores our syslogs and runs <a title=\"A high performance, extremely flexible system for monitoring trends in time-series data.\" href=\"http://cricket.sourceforge.net/\">Cricket</a>, collecting various statistics via SNMP. It also serves as the serial console for most of our Suns, so the machine has to stay up and usable. Over the past couple of weeks performance had degraded steadily. Rebooting would solve the problem at first, but within an hour the load would again be maxxed out, even though the CPU was mostly idle and no RAM was being swapped. I installed a new kernel. I replaced the network card (which was spewing errors). I tried stopping services. I turned off the software RAID. Nothing helped.</p>\n<p>Finally today I stumbled upon the answer. The mail logs from our postfix servers were not being rotated. We had customized syslog to save the mail logs with non-standard names, and my predecessor had put the logrotate configuration for the custom log files not where I'd expect, in /etc/logrotate.conf (which I had copied over from the old system), but in a file named 'guenther' (name changed to protext the guilty) under /etc/logrotate.d/. Cricket was performing a number of greps every 5 minutes on that log file, which by now was at over 200 MB.</p>\n<p>I copied over the file, renamed it guenther_<a title=\"LEO Results for 'blödmann'\" href=\"http://dict.leo.org/?search=bl%F6dmann\">bloedmann</a>, ran a manual logrotate, and everything now seems fine.</p>"},"next":{"fields":{"title":"You're a handsome blog, what's your owner's name?","date":"14 Mar 2004","slug":"/2004-03-14-youre-a-handsome-blog-whats-your-owners-name/"}},"previous":{"fields":{"title":"Tobias is looking for a German greenish kind of liberal New Labour party","date":"15 Mar 2004","slug":"/2004-03-15-tobias-is-looking-for-a-german-greenish-kind-of-liberal-new-labour-party/"}}},{"node":{"fields":{"title":"You're a handsome blog, what's your owner's name?","date":"14 Mar 2004","slug":"/2004-03-14-youre-a-handsome-blog-whats-your-owners-name/","tags":"Y2004"},"html":"<p><a title=\"I'm so bad at remembering names...\" href=\"http://philringnalda.com/blog/2004/03/youre_a_handsome_blog_whats_your_owners_name.php\">You're a handsome blog, what's your owner's name?</a></p>"},"next":{"fields":{"title":"Going Out With a Whimper","date":"14 Mar 2004","slug":"/2004-03-14-going-out-with-a-whimper/"}},"previous":{"fields":{"title":"Another fine moment in systems administration","date":"15 Mar 2004","slug":"/2004-03-15-another-fine-moment-in-systems-administration/"}}},{"node":{"fields":{"title":"Going Out With a Whimper","date":"14 Mar 2004","slug":"/2004-03-14-going-out-with-a-whimper/","tags":"Y2004"},"html":"<p>I'm glad I'm not a Spanish voter. With the tragedy in Madrid just days before todays election, the facts are still unclear, and and least from here it seems both main parties were trying to twist the facts to their advantage. The voters thought the ruling conservatives went too far, and despite leading the polls they <a title=\"BBC NEWS | Europe | Spanish government admits defeat\" href=\"http://news.bbc.co.uk/2/hi/europe/3511280.stm\">appear to have lost</a>, with a large voter turnout. And Aznar bows out not so gracefully, handing power to the leftist oppostition rather than his designated successor. The message of the voters is not one of weakness in the face of terror, but rather an insistance that any government deal honestly with its citizens in time of crisis. Voters by definition are always right. Do not lie to them, they do not take deception lightly.</p>"},"next":{"fields":{"title":"Wikipedia: March 11, 2004 Madrid attacks","date":"12 Mar 2004","slug":"/2004-03-12-wikipedia-march-11-2004-madrid-attacks/"}},"previous":{"fields":{"title":"You're a handsome blog, what's your owner's name?","date":"14 Mar 2004","slug":"/2004-03-14-youre-a-handsome-blog-whats-your-owners-name/"}}}],"pathPrefix":"","first":false,"last":false,"index":510,"pageCount":1101}}

